@import 'src/styles/helpers.scss';

.loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;

  width: 100vw;
  height: 100vh;

  transition: 1.8s $transition-timing-function 0.5s;
  background-color: color(black-alt);

  &.hide {
    transform: translateY(-100%);
    visibility: hidden;
  }
}

.row {
  padding: $col-pad-d;
  @media #{media("<")} {
    padding: rem(12px);
  }
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    position: relative;
    z-index: 0;

    width: calc(#{$col-part-d} * 11 - #{$col-gap-d});
    height: 100%;
    display: flex;
    align-items: flex-end;
    @media #{media("<")} {
      flex-direction: column;
      width: 100%;
      align-items: flex-start;
      justify-content: center;
    }
  }

  &:nth-child(2) {
    width: calc(#{$col-part-d} * 5 - #{$col-gap-d});
    @media #{media("<")} {
      position: absolute;
      top: rem(20px);
      left: 50%;
      transform: translateX(-50%);

      width: auto;
    }
  }
}

.count {
  letter-spacing: -0.06em;
  @include fsl(450px, 90%);
  color: color(white);
  @media #{media("<")} {
    @include fsl(140px);
    letter-spacing: -0.525rem;
  }
}

.line {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;

  width: 4px;
  @media #{media("<")} {
    display: none;
  }
  background-color: #303030;

  &.horizontal {
    position: relative;
    width: 100%;
    height: 4px;
    display: block;
    @media #{media(">")} {
      display: none;
    }

    .fill {
      left: 0;
      right: auto;
      height: 100%;
      width: 0%;

      background: linear-gradient(-90deg, #0000FF -1.21%, rgba(0, 0, 255, 0) 100%);
    }
  }
}

.fill {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;

  height: 0%;
  width: 4px;

  // transition: 2s $transition-timing-function;
  transition: $transition;
  background: linear-gradient(90deg, #0000FF -1.21%, rgba(0, 0, 255, 0) 100%);
}

.text {
  color: color(white);
  letter-spacing: -0.04em;
  @include fsl(80px, 90%);
  @media #{media("<")} {
    @include fsl(32px);
    letter-spacing: -0.06rem;
  }
  animation: blinker 1s linear infinite;
}

@keyframes blinker {  
  50% { opacity: 0; }
}