@import 'src/styles/helpers.scss';

.chat {
  position: fixed;
  right: rem(40px);
  bottom: rem(40px);
  z-index: 100;
  @media #{media("<")} {
    right: rem(13px);
    bottom: rem(13px);
  }

  &.open {

    .link {
      transform: none;
    } 

    .body {
      pointer-events: all;
    }

    .wrapper {
      transform: none;
      opacity: 1;
    }
    .list, .inner {
      transform: none;
      opacity: 1;
      transition-delay: 0.4s;
    }
  }
}

.avatar {
  position: relative;
  z-index: 2;

  
  display: flex;
  align-items: center;

  border-radius: 24px;
  box-shadow: 0px 29px 32px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.img {
  --size: #{100px};
  // @media #{media("<")} {
  //   --size: #{rem(70px)};
  // }

  width: var(--size);
  height: var(--size);
  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 24px;
  overflow: hidden;
}

.pic {
  position: absolute;
  z-index: 1;
  --size: #{82px};
  // @media #{media("<")} {
  //   --size: #{rem(70px)};
  // }

  width: var(--size);
  height: var(--size);

  border-radius: 50%;
  // overflow: hidden;
  border: 3px solid color(white);

  img {
    width: 101%;
    height: 101%;

    border-radius: 50%;
  }
}

.icon {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;

  display: flex;
  align-items: center;
  
  svg {
    --size: #{16px};

    width: var(--size);
    height: var(--size);
  }
}

.links {
  position: absolute;
  bottom: calc(100% + 4px);
  right: 0;
  z-index: 1;

  display: flex;
}

.link {

  display: flex;
  align-items: center;
  transition: $transition;

  &:nth-child(1) {
    transform: translate(30%, 120%);
    transition-delay: 0.2s;
  }
  &:nth-child(2) {
    transform: translate(-30%, 120%);
  }

  &:not(:first-child) {
    margin-left: rem(4px);
  }

  svg {
    --size: #{48px};

    width: var(--size);
    height: var(--size);
  }
}

.body {
  position: absolute;
  bottom: 0;
  right: calc(100% + 6px);

  pointer-events: none;

  &.loading {
    .container {
      transform: translateY(-50px);
      opacity: 0;
      pointer-events: none;
    }
    .loader {
      opacity: 1;
      transition-delay: 0.2s;
    }
  }
}

.container {
  transition: $transition;
}

.list {

  width: 266px;

  opacity: 0;
  transform: translateY(25px);
  transition: $transition;
}

.item { 
  padding: 0 26px;

  width: 100%;
  height: 49px;
  display: flex;
  align-items: center;
  
  font-size: 16px;
  cursor: pointer;
  border-radius: 12px;
  background-color: color(black);
  color: color(white);
  box-shadow: 0px 0px 20px 0px rgba(27, 27, 27, 0.20);

  &:not(:first-child) {
    margin-top: rem(4px);
  }
}

.wrapper {
  padding: 26px;
  margin-top: 6px;

  width: 266px;
  
  background-color: color(white);
  border-radius: 18px;
  box-shadow: 0px 4px 40px 0px rgba(27, 27, 27, 0.16);
  opacity: 0;
  transform: translateX(20%);
  transition: $transition;

  &.big {
    width: 380px;
  }
}

.text {
  
  p {
    font-size: 16px;
    font-weight: 400;

    &:not(:first-child) {
      margin-top: 8px;
    }
  }

  strong {
    font-weight: 700;
  }
}

.inner {
  padding: 8px;

  opacity: 0;
  transform: translateY(25px);
  transition: $transition;
  background-color: color(black);
  border-radius: 12px;
  box-shadow: 0px 0px 20px 0px rgba(27, 27, 27, 0.20);
}

.textarea {
  margin-bottom: 8px;
  padding: 12px;

  width: 100%;
  height: 120px;

  color: color(white);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background: rgba(255, 255, 255, 0.10);
  // @include fsl(16px, 130%);
  font-size: 16px;
  resize: none;
  @include f-main(400);

  &::placeholder {
    color: color(white);
  }
}

.btn {
  margin-top: 6px;

  width: 380px;
}

.offset {
  margin-top: 16px;
}

.input {
  padding: 0 16px;
  
  width: 100%;
  height: 44px;

  transition: $transition;
  color: color(black);
  font-weight: 400;
  // @include fsl(16px, 120%);
  font-size: 16px;
  letter-spacing: -0.02em;
  border-radius: 12px;
  background-color: #F5F5F5;
  border: 1px solid transparent;

  &::placeholder {
    color: color(black);
    font-family: $ff-main;
  }

  &:hover {
    background-color: #EBEBEB;
  }

  &:focus {
    border-color: color(blue);
    background-color: color(white);
  }
  &[aria-invalid="true"] {
    background-color: #FFEAE8;
    border-color: #FFEAE8;
  }
}

.loader {
  position: absolute;
  right: 0;
  bottom: 0;

  width: 64px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 12px;
  background: rgba(11, 11, 11, 0.30);
  box-shadow: 0px 0px 20px 0px rgba(27, 27, 27, 0.20);
  opacity: 0;
  pointer-events: none;
  transition: $transition;
}

.bubble {
  display: flex;
}

.circle {
  width: 5px;
  height: 5px;

  border-radius: 50%;
  background-color: color(white);
  animation: bounce 1.3s linear infinite;

  &:not(:first-child) {
    margin-left: rem(4px);
  }

  &:nth-child(1) {
    animation-delay: .6s;
  }
  &:nth-child(2) {
    animation-delay: .5s;
  }
  &:nth-child(1) {
    animation-delay: .8s;
  }
}

@keyframes bounce {
  30% {
    transform: translateY(-2px);
  }
  60% {
    transform: translateY(0px);
  }
  80% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(0px);
    opacity: .5;
  }
}